<!-- home -->
<template>
  <div class="index-container">
    <div class="warpper">
      <h1 class="demo-home__title"><img src="https://imgs.solui.cn/weapp/logo.png" /><span> VUE H5开发模板</span></h1>
      <h2 class="demo-home__desc">
        A vue h5 template with Vant UI
      </h2>
            <h2 class="demo-home__desc">
        A vue h5 template with Vant UI
      </h2>
       
    </div>


    <Swipe :homeList='homeList' class="s"></Swipe>

<div id="showDays" :class="headerFixed?'issFixed':''">
	
    <van-tabs v-model="active">
      <van-tab title="标签 1">内容 1</van-tab>
      <van-tab title="标签 2">内容 2</van-tab>
      <van-tab title="标签 3">内容 3</van-tab>
    </van-tabs>

</div>

    <!-- <van-cell icon="success" v-for="item in list" :key="item" :title="item.words" /> -->

      <van-list 
        v-model="loading"
        :finished="finished" 							 
        finished-text="没有更多了"						 
        @load="onLoad" 									
        offset="300"									
        :error.sync="error"
        error-text="请求失败，点击重新加载"					
        >
        <van-cell v-for="(item,index) in list" :key="index" :title="item.title" />

      </van-list>  


  </div>
</template>

<script>
import {getHomeList,getTypeList} from '@/api/index'
import Swipe from '@/components/Swipe'
export default {
  data() {
    return {
      list: [
      ],
      loading: false, 		// 是否处在加载状态
      finished: false, 		// 是否已加载完成
      error: false, 		// 是否加载失败
      // list: [],				// 列表
      page: 1,				// 分页
      type:"pop",			// 每页条数
      total: 0,				// 数据总条数
      active:2,
      homeList:[],
      headerFixed:0,
      offsetTop:0

    }
  },
  components:{
    Swipe
  },
  created(){
    getHomeList().then(res=>{
      console.log(res.data.banner.list);
      this.homeList=res.data.banner.list;
    })

    // this.$axios.get('http:x//jfkejfe',{}).then(res=>{
    //   console.log(res);
    // })
    getHomeList({}).then(res=>{
      console.log(res);
    })
    
  },

  computed: {},

  mounted() { 
    // 监听dom渲染完成
		this.$nextTick(function(){
		// 这里fixedHeaderRoot是吸顶元素的ID
    let header = document.getElementById("showDays");
  
		// 这里要得到top的距离和元素自身的高度
		this.offsetTop = header.offsetTop;
		// this.offsetHeight = header.offsetHeight;
		console.log("offsetTop:" + this.offsetTop + "," + this.offsetHeight);
		});
	// handleScroll为页面滚动的监听回调
		window.addEventListener('scroll', this.handleScroll);
  },
  	destroyed(){
		window.removeEventListener('scroll', this.handleScroll);
	},
	

  methods: {
    handleScroll(){
		// 得到页面滚动的距离
		let scrollTop = window.pageYOffset || document.documentElement.scrollTop ||document.body.scrollTop;						
    // 判断页面滚动的距离是否大于吸顶元素的位置
    console.log(scrollTop);
    console.log("divoffsetTOP"+document.getElementById("showDays").offsetTop);

     this.headerFixed=scrollTop>this.offsetTop?1:0

		// this.headerFixed = scrollTop > document.getElementById("showDays").offsetTop;
		},
    // 获取列表数据方法
    async getList() {
      
      let res= await getTypeList({ 
          page: this.page,
          type: this.type,
      })
      console.log(res.data.list)

      if (res.data.list === 0) {  		// 判断获取数据条数若等于0
        this.list = [];				// 清空数组
        this.finished = true;		// 停止加载
      }
      // 若数据条数不等于0
      this.total = res.data.total;		// 给数据条数赋值
      this.list.push(...res.data.list)	// 将数据放入list中
      this.loading = false;			// 加载状态结束 
      // 如果list长度大于等于总数据条数,数据全部加载完成
      console.log(this.list)
      if (this.list.length >= res.data.total) {
          this.finished = true;		// 结束加载状态
      }
    },
	// 被 @load调用的方法
    onLoad() { // 若加载条到了底部
      let timer = setTimeout(() => {	// 定时器仅针对本地数据渲染动画效果,项目中axios请求不需要定时器
        this.getList();					// 调用上面方法,请求数据
        this.page++;					// 分页数加一
        this.finished && clearTimeout(timer);//清除计时器
      }, 100);
    },
	// 加载失败调用方法
    onRefresh() {
      this.finished = false; 		// 清空列表数据
      this.loading = true; 			// 将 loading 设置为 true，表示处于加载状态
      this.page = 1;				// 分页数赋值为1
      this.list = [];				// 清空数组
      this.onLoad(); 				// 重新加载数据
    }
  }
}
</script>
